import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import Img from 'gatsby-image'
import { Card } from 'antd'

import SEO from '../components/SEO'
import Layout from '../layouts/index'
import Container from '../components/Container'
import Space from '../components/Space'
import SocialIcon from '../components/SocialIcon'
import Link from '../components/Link'
import ProjectsSection from '../components/ProjectsSection'
import { Paragraph } from '../styles/typography'
import AdSense from 'react-adsense'

import siteConfig from '../../site-config.js'
import theme from '../styles/theme'
import { rem, media, min } from '../styles/tools'

const jsonLdBreadcrumbSchema = {
  '@context': 'https://schema.org/',
  '@type': 'BreadcrumbList',
  itemListElement: [
    {
      '@type': 'ListItem',
      position: 1,
      name: 'Saijo George',
      item: 'https://saijogeorge.com/',
    },
    {
      '@type': 'ListItem',
      position: 2,
      name: 'Side Project',
      item: 'https://saijogeorge.com/projects.html',
    },
  ],
}

const SocialIcons = ({ horizontal, ...rest }) => (
  <ul
    css={{
      ...(horizontal && {
        display: 'flex',
        '> li:not(:last-of-type)': {
          marginRight: rem(13),
        },
        svg: {
          width: rem(24),
          height: rem(24),
        },
      }),
      'a, svg': {
        display: 'block',
      },
      a: {
        padding: `${rem(theme.space.xs)} 0`,
        color: '#B0C3C9',
        borderRadius: '50%',
        transform: !horizontal && 'rotate(-90deg)',
        transition: '.2s',
        ':hover': {
          color: theme.colors.blue,
          transform: 'rotate(0deg)',
        },
      },
    }}
    {...rest}
  >
    <li>
      <Link
        out
        href={`https://www.linkedin.com/sharing/share-offsite/?url=${url}`}
        title="LinkedIn"
      >
        <SocialIcon icon="linkedIn" />
      </Link>
    </li>
    <li>
      <Link
        out
        href={`https://www.facebook.com/sharer.php?u=${url}`}
        title="Facebook"
      >
        <SocialIcon icon="facebook" />
      </Link>
    </li>
    <li>
      <Link
        out
        href={`https://twitter.com/intent/tweet?url=${url}&text=Tag%20Manager%20JSON%20Generator`}
        title="Twitter"
      >
        <SocialIcon icon="twitter" />
      </Link>
    </li>
    <li>
      <Link
        href={`mailto:?subject=Tag%20Manager%20JSON%20Generator&body=${url}`}
        title="Email"
      >
        <SocialIcon icon="mail" />
      </Link>
    </li>
  </ul>
)

const url = encodeURIComponent(`${siteConfig.siteUrl}${siteConfig.pathPrefix}/`)
const title = 'JSON-LD Schema Generators for SEOs - No Coding Required'
const description =
  'Easy to use JSON-LD schema generators for SEOs, no coding experience required to use these. Use it on your phone or desktop and works on WordPress, WIX, Shopify, etc.'

const IndexPage = () => {
  const data = useStaticQuery(graphql`
    {
      avatar: file(relativePath: { eq: "saijo-george.png" }) {
        childImageSharp {
          fluid(maxWidth: 44, quality: 80) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      faqPageSchemaGenerator: file(
        relativePath: { eq: "faq-page-schema-generator.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 570, quality: 80) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      jsonLdTagManagerFix: file(
        relativePath: { eq: "json-ld-tag-manager-fix.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 570, quality: 80) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `)

  return (
    <Layout title={title} isRoot={true}>
      <SEO title={title} description={description} />
      <Container>
        <Space y={{ mobile: 15, tabletMd: 50 }} />
        <div
          css={{
            display: 'grid',
            gridTemplateColumns: '1fr',
            [media.tabletMd]: { gridTemplateColumns: '1fr auto' },
            gridGap: 30,
          }}
        >
          <div>
            <a
              href="https://saijogeorge.com"
              css={{
                display: 'none',
                [media.max.tablet]: { display: 'flex' },
                alignItems: 'center',
                color: 'rgba(0, 0, 0, 0.85) !important',
                marginBottom: 20,
              }}
            >
              <Img
                fluid={data.avatar.childImageSharp.fluid}
                alt="SaijoGeorge"
                css={{
                  width: 30,
                  height: 30,
                  marginRight: 15,
                  borderRadius: '50%',
                  filter: 'grayscale(1)',
                  transition: '.3s',
                  'a:hover &': {
                    transform: 'scale(1.65)',
                    filter: 'grayscale(0)',
                  },
                }}
              />
              <Paragraph>Created by Saijo George</Paragraph>
            </a>
            <script type="application/ld+json">
              {JSON.stringify(jsonLdBreadcrumbSchema)}
            </script>
            <Paragraph css={{ maxWidth: '63em' }}>
              {
                'Hi everyone, my name is Saijo George and these are my JSON-LD Schema Generators, these are some of my '
              }
              <Link out href="https://saijogeorge.com/projects.html">
                side projects
              </Link>
              {
                " specifically built to help fellow SEOs folks to quickly generate any new Google approved schema markup so that they can get the new rich results for their client's websites. I have made these as simple as possible so you can generate structured data markup in a few simple clicks. You will not need any coding experience to use these and they should work on all mobile and desktop devices."
              }
            </Paragraph>
            <Space y={20} />
            <Paragraph css={{ maxWidth: '63em' }}>
              {
                'If you enjoy using these generators please do consider leaving me a recommendation on '
              }
              <Link out href="https://au.linkedin.com/in/saijogeorge">
                LinkedIn
              </Link>
              {
                ' and if you run into any issues please get in touch with me on '
              }
              <Link out href="http://www.twitter.com/SaijoGeorge">
                Twitter
              </Link>
              {' ðŸ¤™.'}
            </Paragraph>
          </div>
          <div
            css={{
              display: 'flex',
              flexDirection: 'column',
              [media.tabletMd]: { alignItems: 'flex-end' },
            }}
          >
            <Paragraph>Share on social media</Paragraph>
            <Space y={10} />
            <SocialIcons horizontal />
          </div>
        </div>
        <Space y={{ mobile: 30, tabletMd: 70 }} />
        <div>
          <AdSense.Google
            client="ca-pub-4939718182327406"
            slot="5913386672"
            style={{ display: 'block' }}
            layout="in-article"
            format="fluid"
          />
        </div>
        <Space y={{ mobile: 30, tabletMd: 70 }} />
        <div
          css={{
            display: 'grid',
            gridTemplateColumns: '1fr',
            gridGap: rem(20),
            // padding: rem(20),
            [media.tabletLg]: {
              gridTemplateColumns: '1fr 1fr',
              gridGap: rem(30),
              // padding: rem(30),
            },
            [min(1200)]: {
              gridGap: rem(40),
              // padding: rem(40),
            },
            // background: '#fff',
            // borderRadius: 8,
            // boxShadow: '0 5px 30px #CBD6D9',
          }}
        >
          <Link to="/tag-manager-fix/">
            <Card
              hoverable
              cover={
                <Img
                  fluid={data.jsonLdTagManagerFix.childImageSharp.fluid}
                  alt="json-ld-tag-manager-fix"
                />
              }
            >
              <Card.Meta
                title="Tag Manager Fix"
                description="/json-ld-schema-generator/tag-manager-fix/"
              />
            </Card>
          </Link>
          <Link to="/faq/">
            <Card
              hoverable
              cover={
                <Img
                  fluid={data.faqPageSchemaGenerator.childImageSharp.fluid}
                  alt="json-ld-faq-page-schema"
                />
              }
            >
              <Card.Meta
                title="FAQ Generator"
                description="/json-ld-schema-generator/faq/"
              />
            </Card>
          </Link>
        </div>
        <Space y={{ mobile: 30, tabletMd: 70 }} />
        <div>
          <AdSense.Google
            client="ca-pub-4939718182327406"
            slot="5913386672"
            style={{ display: 'block' }}
            layout="in-article"
            format="fluid"
          />
        </div>
      </Container>
      <Space y={{ mobile: 50, tabletMd: 100 }} />
      <ProjectsSection showGenerators={false} />
    </Layout>
  )
}

export default IndexPage
